//   right: 15px;
//   bottom: 20px;
//   display: flex;
//   flex-direction: column;
//   gap: 10px;
//   z-index: 100;
// }
.mapControls {
  position: fixed;
  right: max(12px, env(safe-area-inset-right) + 12px);
  bottom: max(16px, env(safe-area-inset-bottom) + 16px);
  display: flex;
  flex-direction: column;
  gap: 10px;
  z-index: 9999; /* 어떤 유리/블러 레이어보다 위 */
}
.mapControls button {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: none;
  background: white;
  font-size: 20px;
  cursor: pointer;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.2s, transform 0.15s ease;
}
.mapControls button svg {
  stroke: #333;
  transition: stroke 0.2s ease;
  stroke-width: 1.5;
}

.mapControls button:hover {
  background: #d0e6ec;
}
.mapControls button:active {
  transform: scale(0.96);
}
.mapControls button:hover svg {
  stroke: #0077ff;
}
/* 모바일 */
@media (max-width: 600px) {
  .searchBox {
    left: 12px;
  }
  .searchCard {
    width: calc(100vw - 32px);
  }
  .searchInput {
    font-size: 15px;
    padding: 10px 36px;
  }
  /* 모바일에선 과도한 elastic로 넘침 방지 */
  .searchInput:focus {
    transform: none;
  }
  .mapControls button {
    width: 40px;
    height: 40px;
    font-size: 18px;
  }
}
