@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@keyframes indet {
  0% {
    margin-left: -35%;
  }
  50% {
    margin-left: 65%;
  }
  100% {
    margin-left: -35%;
  }
}

.overlay {
  position: fixed;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.28), rgba(0, 0, 0, 0.38));
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  --padX: max(16px, env(safe-area-inset-right), env(safe-area-inset-left));
  --padTop: max(16px, env(safe-area-inset-top));
  --padBottom: max(24px, env(safe-area-inset-bottom));
  padding: var(--padTop) var(--padX) var(--padBottom) var(--padX);
  &[data-visible="false"] {
    display: none;
  }
}

.card {
  width: 100%;
  max-width: 520px;
  box-sizing: border-box;
  color: white;
  background: rgba(255, 255, 255, 0.14);
  border: 1px solid rgba(255, 255, 255, 0.28);
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  padding: 20px;
}

.header {
  display: flex;
  gap: 12px;
  align-items: center;
}
.title {
  font-weight: 600;
  font-size: 16px;
}
.spin {
  width: 22px;
  height: 22px;
  animation: spin 1s linear infinite;
}

.desc {
  margin-top: 6px;
  opacity: 0.9;
  font-size: 13px;
}

.bar {
  --progress: 35%;
  margin-top: 14px;
  height: 8px;
  width: 100%;
  background: rgba(255, 255, 255, 0.22);
  border-radius: 999px;
  overflow: hidden;
  position: relative;
}

.barFill {
  height: 100%;
  width: var(--progress);
  background: white;
  border-radius: 999px;
  transition: width 200ms ease;
}

.indeterminate {
  width: 35%;
  animation: indet 1.4s ease-in-out infinite;
}
.percent {
  margin-top: 6px;
  font-size: 12px;
  opacity: 0.9;
}

@media (prefers-reduced-motion: reduce) {
  .spin {
    animation: none !important;
  }
  .indeterminate {
    animation: none !important;
  }
  .barFill {
    transition: none !important;
  }
}

@media (max-width: 640px) {
  .card {
    max-width: 600px;
    padding: 16px;
    border-radius: 14px;
  }
  .title {
    font-size: 15px;
  }
  .desc {
    font-size: 13px;
  }
  .bar {
    height: 10px;
  }
  .percent {
    font-size: 12px;
  }
}

@media (max-width: 360px) {
  .card {
    padding: 14px;
  }
  .title {
    font-size: 14px;
  }
  .desc {
    font-size: 12px;
  }
}
